import ExamineHeader from '../examine/header'
import ExamineDetail from '../examine/detail'
import axios from 'axios'

export default {
  name: 'examine',
  components: {
    ExamineHeader,
    ExamineDetail
  },
  data () {
    return {
      project: Object,
      name: String
    }
  },
  methods: {
    getProject () {
      axios.get(this.url + '/project/id/' + this.$route.params.pId)
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      console.log(res.status)
      res = res.data
      if (res) {
        this.project = res
        console.log(this.project)
        this.name = res.name
      }
    }
  },
  mounted () {
    this.getProject()
  }
}



