// import axios from 'axios'
export default {
  name: 'assistantForm',
  props: {
    project: Object
  },
  data () {
    return {
      item: {
        name: '',
        creatorID: this.project.creatorID,
        start_time: '',
        end_time: '',
        content: '',
        leaderID: this.project.leaderID,
        targets: this.tags
      },
      list: [],
      target: ''
    }
  },
  methods: {
    getTarget () {
      this.item.targets = this.target.split('，')
    },
    submit () {
      console.log(this.project)
      this.tags = this.project.tagSet
      console.log(this.tags)
      this.tags.forEach(function (item) {
        if (item.id === sessionStorage.getItem('id')) item.tag = true
      })
      this.$axios({
        method: 'post',
        url: this.url + '/project/',
        data: {
          creatorID: this.project.creatorID,
          leaderID: this.project.leaderID,
          startTime: this.item.startTime,
          tagSet: this.tags
        }
      }).then((res) => {
        console.log(res)
        alert('成功创建项目！')
      }).catch((err) => {
        alert(err)
      })
      this.$axios({
        method: 'post',
        url: this.url + '/project/',
        data: JSON.stringify(this.item)
      }).then((res) => {
        console.log(res)
        alert('成功创建项目！')
      }).catch((err) => {
        alert(err)
      })
    }
  }
}



